.container {
  max-width: 1920px;
  padding-left: 68px;
  padding-right: 68px;
  margin: 0 auto;

  @media (max-width: 1600px) {
    max-width: 1600px;
    padding-left: 57px;
    padding-right: 57px;
  }

  @media (max-width: 1280px) {
    max-width: 1280px;
    padding-left: 46px;
    padding-right: 46px;
  }

  @media (max-width: 992px) {
    max-width: 992px;
    padding-left: 40px;
    padding-right: 40px;
  }

  @media (max-width: 767px) {
    max-width: none;
    padding-left: 20px;
    padding-right: 20px;
  }
}


