div.navigation-header {
  width: 1520px;
  display: grid;
  grid-template-columns: min-content max-content auto repeat(2, max-content);
  column-gap: 35px;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 200px;
  padding-right: 200px;
}

.navigation-header__tel {
  display: block;
  font-size: 25px;
  line-height: 28px;
  color: var(--basic-white);
  text-decoration: none;
  margin-left: auto;
  margin-right: auto;
}

.navigation-header__cart {
  position: relative;
  display: flex;
  flex-direction: row;
}

.navigation-header__cart a {
  text-decoration: none;
}

.navigation-header__cart img {
  width: 28px;
  height: 28px;
  object-fit: cover;
}

.navigation-header__cart::before {
  position: absolute;
  content: "";
  width: 30px;
  height: 30px;
  top: -8px;
  left: 20px;
  background-image: url("../img/color-circle.svg");
  background-repeat: no-repeat;
}

.navigation-header__cart span {
  position: relative;
  font-family: inherit;
  font-size: 18px;
  font-variation-settings: "wght" 400, "wdth" 100;
  color: var(--basic-white);
  top: -15px;
  right: 2px;
}

.navigation-header__toggle {
  display: none;
}
