.gallery-card {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(230px, 346px));
  column-gap: 7%;
  row-gap: 50px;

  outline: 1px solid red;

  @media (max-width: 1600px) {

  }
}

.gallery-card__item {
  position: relative;
  display: grid;
  max-width: 346px;
  min-height: 584px;

  outline: 1px solid green;

  @media (max-width: 1600px) {

  }
}

.gallery-card img {
  object-fit: cover;
}

.gallery-card__item::before {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: inherit;
  font-variation-settings: "wght" 400, "wdth" 100;
  width: 122px;
  height: 122px;
  color: var(--basic-white);
  text-align: center;
  top: -40px;
  left: 78%;
}

.gallery-card__item--new::before {
  content: "новинка";
  font-size: 25px;
  line-height: 25px;
  background-color: var(--basic-green);
}

.gallery-card__item--percent::before {
  content: "%";
  font-size: 75px;
  line-height: 55px;
  background-color: var(--basic-yellow);
}

.gallery-card__item--hit::before {
  content: "хит продаж";
  padding-left: 10px;
  padding-right: 10px;
  font-size: 25px;
  line-height: 25px;
  background-color: var(--back-link);
}

.gallery-card__item::after {
  position: absolute;
  width: 100px;
  right: -33px;

  text-align: center;
  color: var(--basic-white);
  z-index: 5;
}


