.field-details {

}

.field-details__list {
  max-width: 100%;
	display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, max-content));
  row-gap: 25px;
  justify-content: space-between;
  margin-top: 25px;
}

.field-details__item label {
  position: relative;
  font-size: 23px;
  font-size: calc(16px + 7*(100vw/1280));
  padding-left: 50px;

  @media (max-width: 480px) {
    font-size: 23px;
  }
}

.field-details__item input[type="checkbox"] + label::before {
  position: absolute;
  content: "";
  width: 35px;
  height: 35px;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  background: transparent;
  border: 1px solid var(--back-input);
}

.field-details__item input[type="checkbox"]:checked + label::after {
  position: absolute;
  content: "";
  top: 0;
  left: 5px;
  width: 25px;
  height: 25px;
  background: url("../img/color-circle.svg");
  background-repeat: no-repeat;
  background-size: 100% auto;
  z-index: 3;
}
