.main-nav {
  justify-content: center;
  column-gap: 36px;
  align-items: center;
}

.main-nav__tel {
  display: block;
  font-size: 25px;
  line-height: 28px;
  color: var(--basic-white);
  text-decoration: none;
  margin-left: auto;
  margin-right: auto;
}

.main-nav__cart {
  position: relative;
  display: flex;
  flex-direction: row;
}

.main-nav__cart a {
  text-decoration: none;
}

.main-nav__cart img {
  width: 28px;
  height: 28px;
  object-fit: cover;
}

.main-nav__cart::before {
  position: absolute;
  content: "";
  width: 30px;
  height: 30px;
  top: -8px;
  left: 20px;
  background-image: url("../img/color-circle.svg");
  background-repeat: no-repeat;
}

.main-nav__cart span {
  position: relative;
  font-family: inherit;
  font-size: 18px;
  font-variation-settings: "wght" 400, "wdth" 100;
  color: var(--basic-white);
  top: -15px;
  right: 2px;
}
